import React, { useCallback, useState } from 'react';
import { Affix, Menu } from 'antd';

export default function() {
  let initCurrentMenu = 'holisticModel';
  if (/section\/1$/.test(window.location.hash)) {
    initCurrentMenu = 'firstFloor';
  } else if (/section\/2$/.test(window.location.hash)) {
    initCurrentMenu = 'secondFloor';
  } else if (/section\/3$/.test(window.location.hash)) {
    initCurrentMenu = 'thirdFloor';
  }
  const [currentMenu, updateCurrentMenu] = useState(initCurrentMenu);

  const clickHandle = useCallback(e => {
    switch (e.key) {
      case 'holisticModel':
        window.location.hash = '/holistic-model';
        break;
      case 'firstFloor':
        window.location.hash = '/section/1';
        break;
      case 'secondFloor':
        window.location.hash = '/section/2';
        break;
      case 'thirdFloor':
        window.location.hash = '/section/3';
        break;
      default:
        window.location.hash = '/holistic-model';
        break;
    }
    updateCurrentMenu(e.key);
  }, []);

  const renderSubMenuTitle = useCallback(() => {
    switch (currentMenu) {
      case 'firstFloor':
        return '剖面图 / 一层';
      case 'secondFloor':
        return '剖面图 / 二层';
      case 'thirdFloor':
        return '剖面图 / 三层';
      default:
        return '剖面图';
    }
  }, [currentMenu]);

  return (
    <Affix offsetTop={0}>
      <Menu selectedKeys={currentMenu} mode="horizontal" onClick={clickHandle}>
        <Menu.Item key="holisticModel">
          {/* <Icon></Icon> */}
          整体模型
        </Menu.Item>
        <Menu.SubMenu title={renderSubMenuTitle()}>
          <Menu.Item key="firstFloor">一层</Menu.Item>
          <Menu.Item key="secondFloor">二层</Menu.Item>
          <Menu.Item key="thirdFloor">三层</Menu.Item>
        </Menu.SubMenu>
      </Menu>
    </Affix>
  );
}
