import React from 'react';
import {
  HashRouter as Router,
  Redirect,
  Route,
  Switch
} from 'react-router-dom';
import Header from 'components/header/Header';
import loadable from '@loadable/component';

const HolisticModel = loadable(() =>
  import('pages/holistic-model/HolisticModel')
);
const SectionPage = loadable(() => import('pages/section/Section'));

export default function() {
  return (
    <React.Fragment>
      <Header />
      <Router>
        <Switch>
          <Route exact={true} path="/">
            <Redirect to="/holistic-model" />
          </Route>
          <Route path="/holistic-model">
            <HolisticModel />
          </Route>
          <Route path="/section/:floorId">
            <SectionPage />
          </Route>
        </Switch>
      </Router>
    </React.Fragment>
  );
}
